nextcloud/vue/dist/Components/NcButton.js'\n\nexport default {\n\tname: 'DetailsExport',\n\tcomponents: {\n\t\tNcButton,\n\t},\n\tprops: {\n\t\ttask: {\n\t\t\ttype: Object,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\texportFormatOptions: ['xml', 'json'],\n\t\t\texportFileFormat: 'xml',\n\t\t}\n\t},\n\tcomputed: {\n\t\tdownloadExportUrl() {\n\t\t\treturn generateUrl(`/apps/mediadc/api/v1/tasks/${this.task.id}/export/${this.exportFileFormat}`)\n\t\t},\n\t},\n\tmethods: {\n\t\tcloseExportResultsDialog() {\n\t\t\tthis.$emit('update:opened', false)\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.fade-enter-active,\n.fade-leave-active {\n\ttransition: opacity 250ms;\n}\n\n.fade-enter,\n.fade-leave-to {\n\topacity: 0;\n}\n\n.fade-visibility-enter,\n.fade-visibility-leave-to {\n\tvisibility: hidden;\n\topacity: 0;\n}\n\n.details-export-wrapper {\n\tposition: fixed;\n\tz-index: 9998;\n\ttop: 0;\n\tleft: 0;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\twidth: 100%;\n\theight: 100%;\n\n}\n\n.details-export {\n\tposition: relative;\n\tborder: 1px solid var(--color-border-dark);\n\tborder-radius: var(--border-radius-large);\n\tbox-shadow: 0 0 30px var(--color-box-shadow);\n\tpadding: 20px 20px 10px;\n\twidth: 100%;\n\tmax-width: 300px;\n\tmargin: auto;\n\tbackground-color: var(--color-main-background);\n}\n\n.details-export-close {\n\tposition: absolute;\n\ttop: 10px;\n\tright: 5px;\n\tpadding: 25px;\n\tbackground: var(--icon-close-dark) no-repeat center;\n\topacity: .5;\n\tcursor: pointer;\n}\n\n.details-export-close:hover {\n\topacity: 1;\n}\n.selection-container {\n\tdisplay: flex;\n\tflex-direction: column;\n\tmargin: 10px auto;\n}\n\n@media (max-width: 540px) {\n\t.details-export {\n\t\twidth: 90vw;\n\t\theight: 80vh;\n\t\toverflow-y: scroll;\n\t\ttop: calc(100% - 96vh);\n\t}\n}\n</style>\n"],"sourceRoot":""}]);
